.blog {
    width: 100vw;
    height: auto;
    padding-top: 80px;
    margin-bottom: 80px;
    background-color: #ffffff;

    .blog-header {
        margin-bottom: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        letter-spacing: 0px !important;
        font-size: 33px;

        img {
            margin-top: -6px !important;
        }
    }

    .top-container {
        margin-top: 40px;
        height: 100%;
        display: flex !important;
        justify-content: center;
        align-items: center;

        .blog-container {
            width: 100%;
            position: relative;

            &::-webkit-scrollbar {
                display: none;
            }

            .blog-card {
                min-height: 500px;
                width: 18rem;
                border-radius: 6px;
                padding: 0px !important;
                display: inline-block !important;
                margin: 20px 0px 0px 0px;
                width: 98.5% !important;
                justify-content: center;
                display: flex;
                white-space: normal;
                box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                -webkit-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                -moz-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);

                &:hover {
                    transition: all 0.15s ease-in-out;
                    transform: scale(1.085);
                    border-color: #ACCEF7;

                }


                img {
                    cursor: pointer;

                    @media screen and (max-width:992px) {
                        height: 215px;
                        object-fit: contain;

                    }
                }

                .blog-body {
                    min-height: 200px;
                    max-height: 430px;
                    overflow: hidden;
                    text-overflow: ellipsis;

                    .blog-information {
                        font-family: sans-serif;
                        color: #8a8a8a;
                        letter-spacing: -0.5px;
                        font-size: 19px;
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        color: black;

                        * {
                            font-weight: bold;
                        }

                        .blog-topic {}

                        .blog-pub-date {}
                    }

                    .blog-title {
                        font-family: inherit;
                        font-weight: bold;
                        letter-spacing: -1.1px;
                        display: -webkit-box;
                        height: 62px;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        font-family: sans-serif;
                        font-size: 22px;
                        line-height: 31px;
                    }

                    .blog-content {
                        font-family: inherit;
                        font-size: 18px;
                        color: #555555;
                        font-weight: normal;
                        letter-spacing: -0.25px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        -webkit-line-clamp: 5;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        line-height: 1.5;
                        max-height: 300px;
                        margin-top: 15px;
                        min-height: 130px;
                    }

                    hr {
                        margin: 0px -10px 0px -10px !important;
                    }

                    .readBtn {
                        width: 100%;
                        height: 50px;
                        position: relative;
                        margin: 5px 0px 5px 0px;
                        top: 5px;
                        font-size: 18px;
                        letter-spacing: 1px;
                        text-decoration: none;
                        font-weight: bold;
                        background-color: #000000;
                        color: white;
                        outline: none;
                        border: none !important;
                        border-radius: 3px;
                    }
                }
            }
        }
    }
}